import { _decorator, Component, Node } from 'cc';
import { IPiece } from './IPiece';
import { PieceTypes } from './PieceTypes';
const { ccclass, property } = _decorator;

@ccclass('Piece')
export class Piece extends Component implements IPiece {
    public row : number = -1!;
    public col : number = -1!;
    public type : PieceTypes = null;

    public init(row : number , col : number , type : PieceTypes){
        this.row = row;
        this.col = col;
        this.type = type;
    }
}

